header {
	position: sticky;
	z-index: 1;
	top: 0px;
	display: grid;
	grid-template-columns: min(40%, 40rem) min(60%, 20rem);
	justify-content: center;
	align-items: center;
	padding: 0.8rem;
	background-color: rgba(255, 255, 255, 0.5);
	font-family: 'Playfair Display', serif;
	font-size: 1.2rem;
}

header a {
	text-decoration: none;
	color: black;
}

header h1 {
	margin-top: -0.3rem;
}
