#home {
	position: absolute;
	z-index: 0;
	min-height: 100vh;
	min-width: 100vw;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
	background-image: url('/src/img/home.jpg');
	background-size: cover;
	background-position: center;
	color: white;
}

#home .left {
	padding-left: max(18%, 8rem);
	text-align: start;
	margin-right: 3rem;
	padding-bottom: 5rem;
}

#home h2 {
	font-family: 'Playfair Display', serif;
	font-size: 3rem;
}

#home button {
	width: 12rem;
	margin-top: 1rem;
}

#home button:hover::before {
	content: '> ';
}

#home button:hover::after {
	content: ' <';
}
